section {
	width: 100%;
	max-width: 420px;
	min-height: 500px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	padding: 0.25rem 1rem;
	margin-top: 3rem;
	background-color: rgba(0, 0, 0, 0.475);
}

form {
	max-width: 260px;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	flex-grow: 1;
	padding-bottom: 1rem;
}

a,
a:visited {
	color: #fff;
}

input[type="text"],
input[type="password"],
input[type="email"],
button,
textarea {
	font-family: 'Nunito', sans-serif;
	font-size: 22px;
	padding: 0.25rem;
	border-radius: 0.5rem;
}

label,
button {
	margin-top: 1rem;
}

button {
	padding: 0.5rem;
}

.instructions {
	width: 100%;
	max-width: 300px;
	font-size: 0.75rem;
	border-radius: 0.5rem;
	background: #000;
	color: #fff;
	/* padding: 0.25rem; */
	position: relative;
	bottom: -10px;
	/* max-width: 420px; */
}

.instructions>svg {
	margin-right: 0.25rem;
}

.offscreen {
	position: absolute;
	left: -9999px;
}

.hide {
	display: none;
}

.valid {
	color: limegreen;
	margin-left: 0.25rem;
}

.invalid {
	color: red;
	margin-left: 0.25rem;
}

.errmsg {
	background-color: lightpink;
	color: firebrick;
	font-weight: bold;
	padding: 0.5rem;
	margin-bottom: 0.5rem;
}

.line {
	display: inline-block;
	font-size: 12pt;
}