.nav-links {
	list-style: none;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.nav-links li {
	margin: 1rem;
}

.nav-links a {
	border: 1px solid transparent;
	color: #FFF;
	text-decoration: none;
	padding: 0.5rem;
}

.nav-links__logout {
	cursor: pointer;
}

.nav-links__logout:hover,
.nav-links__logout:active,
.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
	background: #f8df00;
	border-color: #292929;
	color: #292929;
}

.nav-links button {
	cursor: pointer;
	border: 1px solid #292929;
	color: #292929;
	background: transparent;
	padding: 0.5rem;
	font: inherit;
}

.nav-links button:focus {
	outline: none;
}

.nav-links button:hover,
.nav-links button:active {
	background: #292929;
	color: white;
}